var buttonelement = document.getElementById("Save");
buttonelement.addEventListener("click", TextChanger);
function TextChanger() {
    var operationelement = document.getElementById("Choose");
    var operation = operationelement.value;
    var inputelement = document.getElementById("Input");
    var input = inputelement.value;
    var result = ToUpperLowercase(operation, input);
    var contentelement = document.getElementById("content");
    contentelement.innerHTML = String(result);
}
function ToUpperLowercase(operation, string) {
    var resulttemp;
    switch (operation) {
        case "UpperCase":
            resulttemp = string.toUpperCase();
            break;
        case "LowerCase":
            resulttemp = string.toLowerCase();
            break;
    }
    return resulttemp;
}
